
import { Options, Vue } from 'vue-class-component';

@Options({
  props: {
    msg: String,
  },
  mounted() {
    this.$nextTick(function (this: any) {
      if (this.$route.fullPath === '/explanation/consequence') {
        this.$emit('consequence');
      }
    });
  },
})
export default class Explanation extends Vue {
  msg!: string
}
